@import '~antd/dist/antd.css';

.search-product {
    display: flex;
    margin-bottom: 10px;
    justify-content: center;
}
.search {
    padding: 3px;
    margin-right: 10px;
}
.area-product {
    text-align: center;
    border: 1px solid #ECEAC5;
    width: 35%;
    margin: 0 auto;
    margin-bottom: 20px;
    border-radius: 4px;
    box-shadow: 0 0 10px #EBACFF;
}
.shop {
    margin-bottom: 5px;
}
.area-product .shop img {
    width: 35px;
    height: 35px;
    box-shadow: 0 0 5px red;
}
.product-price {
    display: flex;
    justify-content: center;
}
.product {
}
.product-price .price {
    border-radius: 7px;
    padding:0 2px 0 2px;
    background-color:red;
    color:#fff;
}