.shop__item {
    border:1px solid #eceae7;
    background: #fff;
    color:#323a42;
    margin:0 30px 10px 0;
    width: 180px;
    height: 312px;
    font-size: 13px;
    cursor: pointer;
}
.shop__photo {
    height: 65%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid #eceae7;
}
.shop__photo img {
    width: 104px;
    height: 104px;
}
.retail-root {
    height: 35%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.retail-info {
    margin-bottom: 5px;
}
.retail__offers {
}
.offers {
    font-weight: 600;
}
.retail__shops {
}
.shops {
    font-weight: 600;
}
.retail__shops span {

}
.retail__discount {
    padding: 5px;
    background-color: yellow;
}